  font-family: "Roboto";
  src:
    url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2"),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

:root {
  --header-height: 100px;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

button,
input,
textarea {
  font: inherit;
  color: inherit;
  border: none;
  outline: none;
}

header {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 20px;
  background-color: #1892d3;
  color: white;
  height: var(--header-height);
}

main {
  display: flex;
  min-height: calc(100vh - var(--header-height));
  background-color: #f9f9f9;
}

nav {
  background-color: #e5e7eb;
  width: 300px;
  padding: 20px;
  box-shadow: 4px 0 8px -4px #aaa;
}

.projects-list {
  list-style-type: none;
  margin: 20px 0;
}

.project-button,
.new-project-button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1ch;
  background: none;
  border-radius: 10px;
  width: 100%;
  padding: 10px;
}

.project-button:hover,
.new-project-button:hover {
  background-color: #ccc;
}

.delete-icon {
  display: none;
}

.project-button:hover .delete-icon {
  display: inline-block;
}

.project-button {
  position: relative;
  text-align: left;
}

.project-button.active {
  background-color: #ccc;
  font-weight: bold;
}

.delete-icon {
  position: absolute;
  right: 10px;
}

.new-project-button {
  font-size: 1.2rem;
}

.new-project-button.active {
  display: none;
}

.new-project-form {
  display: none;
  grid-template: 1fr 1fr / 1fr 1fr;
  gap: 10px 20px;
}

.new-project-form.active {
  display: grid;
}

.new-project-form > *,
.todo-form > * {
  padding: 10px;
  border-radius: 10px;
}

.new-project-form > input {
  grid-column: -1 / 1;
  border: 1px solid #aaa;
}

.new-project-form > button {
  font-size: 1rem;
  font-weight: bold;
}

.add,
.save {
  background-color: #afa;
  border: 1px solid #9d9;
}

.add:hover,
.save:hover {
  background-color: #a6f0a6;
}

.cancel {
  background-color: #ffbebe;
  border: 1px solid #eeadad
}

.cancel:hover {
  background-color: #f5b2b2;
}

.project-todos {
  width: 900px;
  padding: 20px 40px;
}

.todo-list {
  list-style-type: none;
  margin: 20px 0;
}

.todo-item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  background-color: #e5e7eb;
  padding: 10px;
  border-radius: 10px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  margin-bottom: 20px;
}

.todo-item button {
  background: none;
}

.todo-item.low {
  border-left-color: green;
}

.todo-item.medium {
  border-left-color: orange;
}

.todo-item.high {
  border-left-color: red;
}

.todo-item.expanded > .bottom {
  display: block;
}

.complete {
  text-decoration: line-through;
  color: #888;
}

.todo-item > .left,
.todo-item > .right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.todo-item > .right {
  justify-content: flex-end;
}

.todo-item > .bottom {
  display: none;
  grid-column: 1 / -1;
  text-align: justify;
  padding: 10px;
  white-space: pre;
}

.new-todo-button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1ch;
  background: none;
  border-radius: 10px;
  width: 100%;
  padding: 10px;
  font-size: 1.2rem;
}

.new-todo-button:hover {
  background-color: #e5e7eb;
}

.todo-form {
  display: grid;
  grid-template: 1fr 2fr 1fr / 3fr 2fr 2fr 3fr;
  gap: 10px;
}

.todo-form > * {
  border: 1px solid #ccc;
}

.todo-form > input[type="text"],
.todo-form > textarea {
  grid-column: 1 / -1;
}

.todo-form > textarea {
  resize: none;
}

.todo-form > .add,
.todo-form > .save,
.todo-form > .cancel {
  font-weight: bold;
}

@media (max-width: 600px) {
  main {
    flex-direction: column;
  }

  nav {
    width: unset;
    box-shadow: 0 4px 8px -4px #aaa;
  }

  .project-todos {
    width: unset;
  }
